import { createSlice } from '@reduxjs/toolkit';
import { getAllVideo, getAllPdf, getAllTip } from './actions';

const PREFIX = 'training';

// initial state
const initialState = {
    videos: [],
    pdfs: [],
    tips: []
};

export const trainingReducer = createSlice({
    name: PREFIX,
    initialState,
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(getAllVideo.fulfilled.type, (state, action) => {
            state.videos = action.payload;
        });
        builder.addCase(getAllPdf.fulfilled.type, (state, action) => {
            state.pdfs = action.payload;
        });
        builder.addCase(getAllTip.fulfilled.type, (state, action) => {
            state.tips = action.payload;
        });
    }
});

export { getAllVideo, getAllPdf, getAllTip };

export default trainingReducer.reducer;
