import { createAsyncThunk } from '@reduxjs/toolkit';
import { backend_base_url } from '../../../config/constants';
import axios from 'axios';

export const getAllVideo = createAsyncThunk('get/all/video', async () => {
    try {
        const response = await axios.get(`${backend_base_url}/training/get/all/video`);
        return response?.data;
    } catch (err) {
        console.log(err);
    }
});

